.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.container {
  height: 760px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main_container {
  border: 1px solid black;
  width: 400px;
  height: 750px;
  background-color: gray;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 15px;
}

h1, h2 {
  margin-bottom: 10px;
  margin-top: 5px;
  font-family: 'roboto';
}
